@import url(https://fonts.googleapis.com/css?family=Amatic+SC:400,700|Montserrat+Alternates:400,700);
@import url(https://fonts.googleapis.com/css?family=Gabriela);
@import url(https://fonts.googleapis.com/css?family=Ruslan+Display);
@import url(https://fonts.googleapis.com/css?family=Underdog);
body {
  margin: 0;
  padding: 0;
  font-family: 'Underdog', cursive;
  background: #515151;
  background: radial-gradient(ellipse at center, #515151 47%,#3a3a3a 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#515151', endColorstr='#3a3a3a',GradientType=1 );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  border-radius: 0 !important;
}

